const htmlTpl = require("Templates/formTemplate/tempText.html");

module.exports = Backbone.View.extend({
    tagName: "div",

    className: "formElement",

    template: _.template(htmlTpl),

    events : {
        "blur input[type='text']" : "onBlurText"
    },

    initialize: function(param) {
        var param = param || {};
        this.setParam(param);
    },

    render: function() {
        this.$el.append(this.template({
            "title" : this.title,
            "value" : this.value,
            "explain" : this.explain,
            "allowEmpty" : this.allowEmpty,
            "name" : this.name,
            "msg" : this.msg,
            "err" : this.err,
            "readonly" : this.readonly
        }));
        return this;
    },
/**
 * 
 * @param {[string]} name    []
 * @param {[object]} options []
 * e : {
 *    title : "a",
 *    value : "content",
 *    explain : "string",
 *    allowEmpty : true,
 *    name : "name"
 * }
 */
    setParam : function(param){
        if(!param){
            return ;
        }

        this.title = param.title || "";//
        this.value = param.value || "";//
        this.explain = param.explain || "";//
        this.allowEmpty = param.allowEmptytrue || true;//
        this.name = param.name || "";//
        this.msg = param.msg || "";//
        this.err = param.err || "";
        this.pattern = param.pattern || null;// 
        this.readonly = param.readonly || false;
    },

    getValue : function(){
        return this.$('input[type="text"]').val()
    },

    setValue : function(val){
        this.value = val;
        this.$('input[type="text"]').val(val);
    },

    setName : function(name){
        this.name = name;
    },

    getName : function(){
        return this.name;
    },

    onBlurText : function(e){
        this.checkValue()?this.$('.formError').hide():this.$('.formError').show();
    },
/**
 * 
 * @return {boolean} [true false]
 */
    checkValue : function(){
        if(!this.pattern) return true;
        var re = this.pattern;
        return re.test(this.getValue());
    }

});


